* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
.header {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 1;
}
.main {
  width: 100%;
  height: 100%;
}
.main__title {
  position: absolute;
  left: 50%;
  top: 120px;
  transform: translateX(-50%);
}
.footer {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
}
.nav {
  width: min-content;
}
.nav__btn-wrap {
  display: none;
}
@media (max-width: 900px) {
  .nav__btn-wrap {
    display: block;
  }
}
.nav__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  transition: all 0.5s ease-in-out;
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  z-index: 1;
  cursor: pointer;
}
.nav__btn-burger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 6px;
  background-color: #000;
  border: none;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.nav__btn-burger::before,
.nav__btn-burger::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 6px;
  background-color: #000;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.nav__btn-burger--open {
  background-color: #ffffff;
}
.nav__btn-burger--open::before,
.nav__btn-burger--open::after {
  background-color: #ffffff;
}
.nav__btn-burger::before {
  transform: translateY(-16px);
}
.nav__btn-burger::after {
  transform: translateY(16px);
}
.nav__btn--open .nav__btn-burger {
  transform: translateX(-50px);
  background: transparent;
}
.nav__btn--open .nav__btn-burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.nav__btn--open .nav__btn-burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
.nav__list {
  display: block;
  width: 100%;
  height: 100%;
}
@media (max-width: 900px) {
  .nav__list {
    transition: all 0.5s ease-in-out;
    opacity: 0;
    pointer-events: none;
  }
}
.nav__list--open {
  transition: all 0.5s ease-in-out;
  opacity: 1;
  pointer-events: visible;
}
.nav__background-notouch {
  display: none;
}
.nav__background-notouch--on {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(60, 60, 60, 0.5);
}
.nav__list-wrap {
  display: grid;
  grid-template-columns: repeat(5, min-content);
  grid-column-gap: 20px;
  width: 100%;
  height: 100%;
}
@media (max-width: 900px) {
  .nav__list-wrap {
    position: absolute;
    top: 0;
    left: 0;
    padding: 80px 10px 20px 10px;
    width: 100%;
    height: min-content;
    background-color: rgba(60, 60, 60, 0.9);
    color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}
.nav__elem {
  list-style: none;
}
@media (max-width: 900px) {
  .nav__elem {
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
    text-align: center;
  }
}
.nav__link {
  color: #000000;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 0.9;
  text-decoration: underline;
  text-decoration-thickness: 4px;
  color: tomato;
}
.nav__link:active {
  opacity: 0.7;
  text-decoration: underline;
  text-decoration-thickness: 4px;
  color: tomato;
}
@media (max-width: 900px) {
  .nav__link {
    display: inline-block;
    color: #ffffff;
    font-size: 24px;
    letter-spacing: 1px;
    width: 100%;
    border-bottom: 4px solid transparent;
  }
  .nav__link:hover,
  .nav__link:focus,
  .nav__link:active {
    opacity: 0.9;
    text-decoration: none;
    border-bottom: 4px solid tomato;
  }
}
.slider {
  width: 100%;
  height: 100vh;
  padding: 20px 20px;
  display: grid;
  grid-gap: 20px;
  justify-items: center;
  align-content: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  background-color: #ffffff;
}
@media (max-width: 600px) {
  .slider {
    width: 100%;
    padding: 20px 0;
  }
}
.sleder-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(4, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.sleder-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.sleder-controls__button {
  width: 80px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.sleder-controls__button-img {
  width: 100%;
  height: 100%;
}
.sleder-controls__button-img:hover {
  opacity: 0.7;
}
.sleder-controls__button-img:active {
  opacity: 0.3;
}
.sleder-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.sleder-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.sleder-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.slider-items {
  width: 500px;
}
@media (max-width: 600px) {
  .slider-items {
    width: 100%;
  }
}
.slider-items__item {
  text-align: center;
}
.slider-items__item--hidden {
  display: none;
}
.slider-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .slider-items__img {
    width: 100%;
  }
}
.sliderfull {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #F5F5F5;
  position: relative;
}
.sliderfull__wrap {
  position: relative;
  padding: 20px 20px;
  display: grid;
  grid-gap: 10px;
  justify-items: center;
  align-content: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
}
@media (max-width: 600px) {
  .sliderfull__wrap {
    padding: 20px 0;
  }
}
.slederfull-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slederfull-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.slederfull-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.slederfull-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.slederfull-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #fff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
@media (max-width: 600px) {
  .sliderfull-indicators__button {
    width: 30px;
    height: 10px;
  }
}
.sliderfull-indicators__button--active {
  background-color: tomato;
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 500px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 600px) {
  .sliderfull-items {
    width: 100%;
  }
}
.sliderfull-items__inner {
  display: flex;
  transition: 0.6s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .sliderfull-items__img {
    width: 100%;
  }
}
.tabs {
  padding: 20px 20px;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.tabs__wrap {
  display: grid;
  grid-template-rows: 1fr min-content;
  grid-row-gap: 20px;
  justify-items: center;
  align-content: center;
}
.tab {
  display: none;
}
.tab--current {
  display: block;
}
.tab__wrap {
  display: grid;
  grid-template-columns: 100px 500px;
  grid-column-gap: 25px;
  width: 100%;
}
@media (max-width: 622px) {
  .tab__wrap {
    grid-template-rows: min-content 1fr;
    grid-template-columns: 1fr;
    grid-gap: 10px;
  }
}
.tab__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
@media (max-width: 622px) {
  .tab__title {
    align-items: center;
  }
}
.tab__img {
  align-self: center;
  object-fit: contain;
  background-color: #fff;
  width: 100%;
  height: 200px;
}
/*tabs controls radio btn styles*/
.tabs-controls {
  justify-self: center;
  border: none;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(3, min-content);
  grid-column-gap: 20px;
  margin: 0;
  padding: 0;
}
.tabs-controls__btn {
  position: absolute;
  appearance: none;
}
.tabs-controls__btn:checked ~ .tabs-controls__custom-elem::before {
  position: absolute;
  content: '';
  width: 25px;
  height: 25px;
  background-color: #C0C0C0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}
.tabs-controls__btn[data-tab-name="tab-3"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: #FFFF00;
}
.tabs-controls__btn[data-tab-name="tab-2"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: #0000FF;
}
.tabs-controls__btn[data-tab-name="tab-1"]:checked ~ .tabs-controls__custom-elem::before {
  background-color: tomato;
}
.tabs-controls__label {
  display: flex;
}
.tabs-controls__custom-elem {
  width: 48px;
  height: 48px;
  margin-right: 23px;
  background-color: #AFEEEE;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  margin: 0;
}
.popup-block {
  width: 100%;
  height: 100vh;
  background-color: #F5F5F5;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-block__btn {
  width: 200px;
  height: 80px;
  border: 2px solid #DCDCDC;
  border-radius: 5px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 22px;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #ffffff;
  transition: 0.5s all;
}
.popup-block__btn:hover,
.popup-block__btn:focus {
  font-size: 25px;
  border: 2px solid #CCFFCC;
}
.popup-block__btn:active {
  transition: 0s all;
  opacity: 0.7;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.popup--target {
  opacity: 1;
  visibility: visible;
}
.popup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
}
.popup__content {
  position: relative;
  width: 400px;
  height: 200px;
  background-color: #ffffff;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 600px) {
  .popup__content {
    width: 60%;
  }
}
.popup__close-btn {
  width: 30px;
  height: 30px;
  position: absolute;
  right: -40px;
  top: 0;
  background-color: transparent;
  opacity: 0.8;
  border: none;
  cursor: pointer;
  transition: 0.6s all;
}
.popup__close-btn:hover,
.popup__close-btn:focus {
  transform: rotate(180deg);
  opacity: 1;
}
.popup__close-img {
  width: 100%;
  height: 100%;
}
.pagination {
  padding: 20px 20px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.table {
  background-color: #ffffff;
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
}
.table--head {
  color: #000000;
  font-size: 19px;
  font-weight: bold;
  text-transform: uppercase;
}
.table--row {
  font-size: 19px;
  background-color: #e6e1e1;
  color: #000000;
  text-transform: uppercase;
  word-wrap: break-word;
}
.table__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  color: purple;
  margin-bottom: 15px;
}
@media (max-width: 900px) {
  .table__title {
    text-align: left;
  }
}
.table__head {
  background-color: #f0f2f2;
}
.table__body > .table__row {
  color: #000000;
}
.table__body > .table__row:nth-child(even) {
  background-color: #fcf5f5;
}
.table__body > .table__row:nth-child(odd) {
  background-color: #f7f2f2;
}
.table__item {
  text-align: center;
  vertical-align: baseline;
  padding: 20px;
  font-size: 18px;
  letter-spacing: 1px;
  height: 70px;
}
.table__item:nth-child(1) {
  width: 35%;
  text-align: left;
}
.table__item:nth-child(2) {
  width: 40%;
}
.table__item:nth-child(3) {
  width: 25%;
}
@media (max-width: 900px) {
  .table__item {
    font-size: 17px;
    padding: 15px;
  }
}
@media (max-width: 600px) {
  .table__item {
    font-size: 15px;
    padding: 10px;
  }
}
.pagination-controls {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  list-style: none;
  margin-top: 20px;
}
.pagination-controls__elem {
  margin-right: 15px;
  margin-bottom: 15px;
}
.pagination-controls__btn {
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 3px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  font-family: "PT Sans", "Arial", sans-serif;
}
.pagination-controls__btn--active {
  background-color: #DCDCDC;
  font-size: 22px;
}
.pagination-controls__btn:hover,
.pagination-controls__btn:focus {
  font-size: 22px;
  font-weight: bold;
}
